.form-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

h2 {
  text-align: left;
  color: #333;
}

.form-group {
  margin-bottom: 15px;
  width: 30%;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  text-align: left; /* Align labels to the left */
}

.input-field,
.checkbox-field,
.select-field {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 14px;
  text-align: left; /* Align input fields to the left */
}

.checkbox-field + label {
  margin-left: 5px;
}

.submit-button {
  width: 20%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.submit-button:hover {
  background-color: #0056b3;
}

.checkbox-container {
  margin-left: 20px; /* Adjust the left margin as needed */
}

.checkbox-option {
  margin-bottom: 5px; /* Add space between options */
}

.checkbox-option input[type="checkbox"] {
  margin-right: 5px; /* Add space between checkbox and label */
}

.checkbox-option label {
  font-weight: 10%; /* Remove bold text */
  font-size: 12px; /* Make the font size smaller */
  margin-left: 5px; /* Add some space between checkbox and label */
}

.longanswer-field {
  width: 100%; /* Make the textarea full width */
  height: 40px; /* Set the height to your desired value */
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 14px;
  resize: vertical; /* Allow vertical resizing */
}


